.main {
  padding: 20px;
  max-width: 1024px;
  background-color: #fff;
  margin: 0px auto 70px;
  text-align: left;
  
  &__nav {
    display: block;
    box-sizing: border-box;
    border-bottom: 1px solid #dddddd;
    
    padding: 70px 0 5px 0;
    margin: 70px auto 35px;
    max-width: 1024px;
    text-align: left;
    text-transform: uppercase;
    font-size: 12px;

    &__title {
      display: block;
      margin: 0;
    }
  }
}


/* responsive settings */

/* Mobile: 1 coluna */
.main__shop {
  box-sizing: border-box;
  display: grid;
  grid-template-columns: 1fr;
  gap: 20px;
  max-width: 1024px;
  margin: 0px auto 70px;
  justify-items: center;
  justify-content: center;
}
.main__shop > * {
  max-width: 241px;
}

.header__content__nav__buttons {
  display: none;
}
.header__content__logo__text {
  display: none;
}
.header__content__cart__view { 
  display: none;
}
.header__content__cart__icon {
  margin-right: 15px;
}
.header__content__cart__menu__icon {
  display: block;
}

/* Tablet: 2 colunas (largura mínima de 576px) */
@media (min-width: 576px) {
  .main__shop {
    grid-template-columns: repeat(2, minmax(0, 241px));
    justify-items: center;
    justify-content: center;
  }
}

/* Desktop: 4 colunas (largura fixa de 1024px com gaps) */
@media (min-width: 1024px) {
  .main__shop {
    grid-template-columns: repeat(4, minmax(0, 241px));
    justify-items: center;
    justify-content: center;
  }

  .header__content__nav__buttons {
    display: flex;
  }
  .header__content__logo__text {
    display: block;
  }
  .header__content__cart__view {
    display: block;
  }
  /*
  .header__content__cart__icon {
    margin-right: 0;
  }
  */
  .header__content__cart__menu__icon {
    display: none;
  }
}
